import React, { useContext } from 'react';
/* import logoImg from '../../assets/canal10.png'; */
import { AuthContext } from '../../context/AuthContext';
import { LoginComp } from './LoginComp';
import { RegisterComp } from './RegisterComp';

export const NavComp = () => {
  const { currentUser, logout } = useContext(AuthContext);

  return (
    <nav className="container-fluid navbar navbar-light bg-danger">
      <div className="container-fluid">
        <div className="navbar-brand">
          {/* <img src={logoImg} alt="logo" height="50" /> */} TALLER DE TAXIS
        </div>
        <div className="d-flex">
          <div className="col">
            {currentUser ? (
              <>
                <div className="btn mx-2">
                  Encargado en turno: <span className="p-2 disabled">
                    { currentUser.email.slice(0, -10)}</span>
                </div>
                <div onClick={() => logout()} className="btn mx-2">
                  SALIR
                </div>
              </>
            ) : (
              <>
                <LoginComp />
                <RegisterComp />
              </>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
};
